.header {
    min-height: 5vw;
    hr {
        border: 1px solid #223046;
    }
}

.header-wrap {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
}

.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    a {
        text-decoration: none;
    }
    h2 {
        font-family: "Lato", sans-serif;
        color: #223046;
        font-size: 1.6vw;
        cursor: pointer;
    }
}

.person {
    display: flex;
    justify-content: center;
    align-items: center;
    i {
        color: #223046;
        font-size: 1.6vw;
        cursor: pointer;
    }
}