.sidebar {
    border: 1px solid #bfc4cd;
}

.sidebar-head {
    border-bottom: 1px solid #bfc4cd;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebar-content {
    ul {
        list-style: none;
        margin: 0;
        padding: 0;
        li {
            padding-left: 25%;
            border-bottom: 1px solid #bfc4cd;
            display: block;
            display: flex;
            justify-content: flex-start;
            img {
                width: 20px;
                height: 15px;
            }
        }
    }
}